// context/AuthContext.js
import { createContext, useContext, useEffect, useState } from "react";

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  // üìå Fake Users (ÿ≥Ÿäÿ™ŸÖ ÿßÿ≥ÿ™ÿ®ÿØÿßŸÑŸáŸÖ ŸÑÿßÿ≠ŸÇÿßŸã ÿ®ÿßŸÑÿ®ÿßŸÉ ÿßŸÜÿØ)
  const fakeUsers = [
    {
      username: "admin",
      password: "123",
      role: "admin",
      permissions: [
        "view_dashboard",
        "manage_users",
        "manage_medicines",
        "view_sales",
        "add_sale",
        "view_inventory",
        "view_reports",
        "print_reports",
        "shift_manage",
      ],
    },
    {
      username: "ph",
      password: "123",
      role: "pharmacist",
      permissions: [
        "manage_medicines",
        "view_inventory",
        "add_sale",
        "view_sales",
      ],
    },
    {
      username: "ca",
      password: "123",
      role: "cashier",
      permissions: ["add_sale", "view_sales", "shift_manage"],
    },
  ];

  // ‚úî ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑÿ¨ŸÑÿ≥ÿ© ŸÖŸÜ localStorage
  useEffect(() => {
    const saved = localStorage.getItem("pharm_user");
    if (saved) {
      setUser(JSON.parse(saved));
    }
    setLoading(false);
  }, []);

  // ‚úî ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ
  const login = (username, password) => {
    const found = fakeUsers.find(
      (u) => u.username === username && u.password === password
    );

    if (!found) return { success: false, message: "ÿ®ŸäÿßŸÜÿßÿ™ ÿßŸÑÿØÿÆŸàŸÑ ÿ∫Ÿäÿ± ÿµÿ≠Ÿäÿ≠ÿ©" };

    const loggedUser = {
      username: found.username,
      role: found.role,
      permissions: found.permissions,
    };

    localStorage.setItem("pharm_user", JSON.stringify(loggedUser));
    setUser(loggedUser);

    return { success: true };
  };

  // ‚úî ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿÆÿ±Ÿàÿ¨
  const logout = () => {
    localStorage.removeItem("pharm_user");
    setUser(null);
  };

  // ‚úî ŸÅÿ≠ÿµ ÿµŸÑÿßÿ≠Ÿäÿ© ŸÖÿπŸäŸÜÿ©
  const hasPermission = (perm) => {
    if (!user || !user.permissions) return false;
    return user.permissions.includes(perm);
  };

  // ‚úî ŸÅÿ≠ÿµ ÿµŸÅÿ≠ÿ© ÿ≠ÿ≥ÿ® ŸÖÿ≥ÿßÿ±Ÿáÿß
  const routePermissions = {
    "/dashboard": ["view_dashboard"],
    "/users": ["manage_users"],
    "/products": ["manage_medicines", "view_inventory"],
    "/inventory": ["view_inventory"],
    "/sales": ["add_sale", "view_sales"],
    "/reports": ["view_reports"],
    "/accounts": ["manage_accounts"],
    "/shift": ["shift_manage"],
  };

  const canAccessRoute = (path) => {
    const required = routePermissions[path];
    if (!required) return true; // ÿµŸÅÿ≠ÿ© ÿπÿßŸÖÿ© ÿ£Ÿà ŸÑÿß ÿ™ÿ≠ÿ™ÿßÿ¨ ÿµŸÑÿßÿ≠Ÿäÿßÿ™

    if (!user?.permissions) return false;

    return required.some((perm) => user.permissions.includes(perm));
  };

  return (
    <AuthContext.Provider
      value={{
        user,
        loading,
        login,
        logout,
        hasPermission,
        canAccessRoute,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);


















